@import url("./assets/css/fonts_collection.css");

:root {
  --font-base: "Arima", serif;
  --font-alt: "Open Sans", sans-serif;
  --font-b: "Barriecito", serif;
  --font-a: "Arima", serif;
  --font-c: "Be Vietnam Pro", serif;
  --font-d: "Bevan", serif;
  --font-e: "Alata", serif;
  --font-f: "Atomic Age", serif;
  --font-g: "Autour One", serif;
  /* --font-arima: "Arima", serif; */

  --color-golden: #dcca87;
  --color-black: #0c0c0cfa;
  --color-gray: #545454;
  --color-crimson: #f5efdb;
  --color-grey: #aaaaaa;
  --color-white: #ffffff;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

a {
  color: var(--color-golden) !important;
  text-decoration: none;
}

.slide-bottom {
  -webkit-animation: slide-bottom 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: slide-bottom 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

@-webkit-keyframes slide-bottom {
  0% {
    -webkit-transform: translateY(-25%);
    transform: translateY(-25%);
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@keyframes slide-bottom {
  0% {
    -webkit-transform: translateY(-25%);
    transform: translateY(-25%);
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}
