.app__home {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
}

.app__home_heading {
  padding: 4em;
}

.app__home_cards {
  justify-content: center;
  align-items: center;
  align-self: center;
  display: flex;
  flex-direction: column;
}

.SI {
  width: 10px;
  height: 10px;
  border-radius: 5px;
  color: white;
  border: 1px solid var(--color-golden);
  margin-top: 3em;
}
