.app__Category {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
}

.app__Category_title_div {
  flex-direction: column;
}
.app__Category_title_div {
  flex-direction: row;
}

.app__Category-title {
  color: rgba(9, 9, 98, 0.803);
  font-family: var(--font-d);
  width: 100% !important;
  text-transform: capitalize;
  border-bottom: 1px solid var(--color-black);
}

.app__Category-num {
  color: var(--color-black);
  font-family: var(--font-a);
}

.app__Category-benefit {
  color: rgba(9, 9, 98, 0.903);
  font-family: var(--font-c);
}

.app__Category-buybtn {
  box-shadow: 0px 0px 3px 0px rgb(0, 0, 0);
  background-color: rgba(255, 0, 0, 0.407);
  margin: 0.3em;
}

@media screen and (max-width: 450px) {
  .app__Category {
    flex-direction: column;
  }
}
