.app__SubAward {
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-bottom: 1px solid var(--color-golden);
  flex-direction: row;
}

.app__SubAward_title_div {
  flex-direction: column;
}
.app__SubAward_title_div {
  flex-direction: row;
}

.app__SubAward-title {
  color: var(--color-golden);
  font-family: var(--font-d);
  width: 100% !important;
  text-transform: capitalize;
}

.app__SubAward-category {
  color: var(--color-crimson);
  font-family: var(--font-a);
}

.app__SubAward-desc {
  color: var(--color-white);
  font-family: var(--font-c);
}

.app__SubAward-eligibility {
  color: var(--color-crimson);
  font-family: var(--font-e);
}

.app__SubAward-applybtn,
.app__SubAward-votebtn {
  box-shadow: 0px 0px 3px 0px rgb(0, 0, 0);
  background-color: rgba(255, 255, 255, 0.14);
  margin: 0.3em;
}

.app__SubAward-votebtn {
  background-color: rgba(255, 0, 0, 0.242);
}

@media screen and (max-width: 450px) {
  .app__SubAward {
    flex-direction: column;
  }
}
