/* arima-regular - latin */
@font-face {
  font-family: "Arima";
  font-style: normal;
  font-weight: 400;
  src: url("fonts/Arima/arima-v1-latin-regular.eot"); /* IE9 Compat Modes */
  src: local(""),
    url("fonts/Arima/arima-v1-latin-regular.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("fonts/Arima/arima-v1-latin-regular.woff2")
      format("woff2"),
    /* Super Modern Browsers */ url("fonts/Arima/arima-v1-latin-regular.woff")
      format("woff"),
    /* Modern Browsers */ url("fonts/Arima/arima-v1-latin-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("fonts/Arima/arima-v1-latin-regular.svg#Arima") format("svg"); /* Legacy iOS */
}

