.app__card {
  width: 40%;
  border: 1px solid var(--color-crimson);
  border-radius: 2em;
  display: flex;
  flex-direction: column;
  padding: 2em;
  box-shadow: 0px 0px 5px 5px rgb(0, 0, 0);
}

.app__card-name {
  font-family: var(--font-f);
  padding: 20px;
  color: var(--color-golden);
  border-bottom: 1px solid var(--color-golden);
}

.app__card_img_div {
  padding: 40px;
  align-items: center !important;
  align-self: center !important;
  justify-content: center !important;
}

.app__card_img_div img {
  width: 100%;
}

.app__card_desc {
  font-size: 13px;
  color: white;
  font-family: var(--font-f);
  letter-spacing: 1.3px;
  text-align: center;
}

.app__card_info {
  line-height: 55px;
  font-size: 12px;
  font-style: italic;
  font-family: var(--font-f);
  opacity: 0.8;
  letter-spacing: 0.5px;
  color: gold;
}

.app__card_action_btn {
  box-shadow: 0px 0px 5px 5px black;
  color: var(--color-golden);
  font-family: var(--font-f);
  padding: 1em 0;
  border: 0.5px solid var(--color-golden);
  border-radius: 2em;
  cursor: pointer;
}

.app__card_action_btn:hover {
  color: var(--color-white);
}

@media screen and (min-width: 2000px) {
  .app__card-name {
    font-size: 3em;
  }

  .app__card_img_div img {
    width: 80%;
  }

  .app__card_desc {
    font-size: 25px;
    margin-top: 2em;
  }

  .app__card_info {
    margin-top: 1em;
    font-size: 20px;
  }

  .app__card_action_btn {
    font-size: 30px;
  }
}

@media screen and (max-width: 1450px) {
  .app__card {
    width: 45%;
  }
  .app__card-name {
    font-size: 2em;
  }

  .app__card_img_div img {
    width: 80%;
  }

  .app__card_desc {
    font-size: 19px;
    margin-top: 2em;
  }

  .app__card_info {
    margin-top: 1em;
    font-size: 18px;
  }

  .app__card_action_btn {
    font-size: 20px;
  }
}

@media screen and (max-width: 1024px) {
  .app__card {
    width: 50%;
  }
  .app__card-name {
    font-size: 2em;
  }

  .app__card_img_div img {
    width: 89%;
  }

  .app__card_desc {
    font-size: 16px;
    margin-top: 2em;
  }

  .app__card_info {
    margin-top: 1em;
    font-size: 18px;
  }

  .app__card_action_btn {
    font-size: 20px;
  }
}

@media screen and (max-width: 780px) {
  .app__card {
    width: 60%;
  }
  .app__card-name {
    font-size: 2em;
  }

  .app__card_img_div img {
    width: 90%;
  }

  .app__card_desc {
    font-size: 12px;
    margin-top: 2em;
    font-family: var(--font-c);
  }

  .app__card_info {
    margin-top: 1em;
    font-size: 11px;
  }

  .app__card_action_btn {
    font-size: 12px;
  }
}
@media screen and (max-width: 450px) {
  .app__card {
    width: 80%;
  }
  .app__card-name {
    font-size: 2em;
  }

  .app__card_img_div img {
    width: 170%;
    margin-left: -2em;
  }

  .app__card_desc {
    font-size: 13px;
    margin-top: 1.5em;
    font-family: var(--font-c);
  }

  .app__card_info {
    margin-top: 1em;
    font-size: 14px;
    line-height: 8px;
  }

  .app__card_action_btn {
    font-size: 11px;
  }
}
