.app__headtext {
  display: flex;
  flex-direction: column;
  padding: 1em;
  width: 200px;
}

.app__headtext-txt {
  color: var(--color-golden);
  font-family: var(--font-b);
  font-size: 90px;
  width: 150%;
}

.app__headtext-subtxt {
  color: white;
  font-family: var(--font-d);
  letter-spacing: 1.2px;
  padding-top: 2em;
  width: 55%;
  font-style: italic;
}

.subtxt2 {
  font-family: var(--font-e);
  padding-top: 0em;
}

@media screen and (min-width: 2000px) {
  .app__headtext-txt {
    font-size: 150px;
    width: 100%;
  }

  .app__headtext-subtxt {
    letter-spacing: 1.5px;
    line-height: 50px;
    font-size: 28px;
    width: 100%;
  }
}

@media screen and (max-width: 1450px) {
  .app__headtext-txt {
    font-size: 80px;
    width: 450%;
  }

  .app__headtext-subtxt {
    letter-spacing: 1.5px;
    line-height: 50px;
    font-size: 24px;
    width: 450%;
  }
}

@media screen and (max-width: 1024px) {
  .app__headtext-txt {
    font-size: 80px;
    width: 100%;
  }

  .app__headtext-subtxt {
    letter-spacing: 1.5px;
    line-height: 45px;
    font-size: 20px;
    width: 100%;
  }
}

@media screen and (max-width: 780px) {
  .app__headtext-txt {
    font-size: 55px;
    width: 100%;
  }

  .app__headtext-subtxt {
    letter-spacing: 1.5px;
    line-height: 45px;
    font-size: 18px;
    width: 100%;
    font-weight: normal;
  }
}
@media screen and (max-width: 450px) {
  .app__headtext {
    padding: 1em 0em;
  }

  .app__headtext-txt {
    font-size: 34px;
    width: 120%;
  }

  .app__headtext-subtxt {
    letter-spacing: 1.5px;
    line-height: 30px;
    font-size: 17px;
    width: 100%;
    font-weight: normal;
  }

  .subtxt2 {
    width: 150%;
  }
}
